import axios from "axios";

// ê¸°ë³¸ ì„¤ì •: í˜„ì¬ í˜¸ìŠ¤íŠ¸ ì •ë³´ë¥¼ ë™ì ìœ¼ë¡œ ì„¤ì •
axios.defaults.baseURL = `${window.location.protocol}//${window.location.hostname}:8081`; // Spring Boot ì„œë²„ì˜ ë„¤íŠ¸ì›Œí¬ ì£¼ì†Œ
axios.defaults.headers.post["Content-Type"] = "application/json";

// ìš”ì²­ ì¸í„°ì…‰í„° ì¶”ê°€
axios.interceptors.request.use(
  (config) => {
    // localStorageì—ì„œ í† í° ê°€ì ¸ì˜¤ê¸°
    const token = localStorage.getItem("token");
    if (token) {
      // Authorization í—¤ë”ì— í† í° ì¶”ê°€
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// ì‘ë‹µ ì¸í„°ì…‰í„° ì¶”ê°€ (ì˜µì…˜)
axios.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    if (error.response && error.response.status === 401) {
      // 401 Unauthorized ë°œìƒ ì‹œ ì²˜ë¦¬ (ì˜ˆ: ë¡œê·¸ì¸ í˜ì´ì§€ë¡œ ì´ë™)
      alert("ì¸ì¦ì´ ë§Œë£Œë˜ì—ˆìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸í•´ì£¼ì„¸ìš”.");
      localStorage.removeItem("token");
      window.location.href = "/login";
    }
    return Promise.reject(error);
  }
);

export default axios;

